import React, { useState, useEffect, useRef } from 'react';

const CameraCaptureWithVideo = () => {
  const [capturedImage, setCapturedImage] = useState(null); // State to store the captured image URL
  const videoRef = useRef(null); // Ref for the video element
  const canvasRef = useRef(null); // Ref for the canvas element

  useEffect(() => {
    // Access the camera and start the video stream
    const startVideo = async () => {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: true, // Access video only (camera)
        });

        if (videoRef.current) {
          videoRef.current.srcObject = stream;
        }
      } catch (err) {
        console.error('Error accessing camera: ', err);
      }
    };

    startVideo(); // Start the video stream when the component mounts

    // Clean up the stream when the component unmounts
    return () => {
      if (videoRef.current) {
        const stream = videoRef.current.srcObject;
        const tracks = stream?.getTracks();
        tracks?.forEach(track => track.stop()); // Stop the stream
      }
    };
  }, []);

  // Function to capture a photo from the video stream
  const capturePhoto = () => {
    const video = videoRef.current;
    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');

    // Set canvas size to match the video size
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;

    // Draw the current frame from the video onto the canvas
    ctx.drawImage(video, 0, 0, canvas.width, canvas.height);

    // Get the image data URL from the canvas
    const imageUrl = canvas.toDataURL('image/png');
    setCapturedImage(imageUrl); // Store the captured image
  };

  return (
    <div>
      {/* Video element to show the live camera feed */}
      <video
        ref={videoRef}
        autoPlay
        playsInline
        width="100%"
        height="auto"
        style={{ border: '1px solid black', marginBottom: '10px' }}
      />
      
      {/* Button to capture a photo from the video */}
      <button onClick={capturePhoto} style={{ padding: '10px', margin: '10px 0' }}>
        Capture Photo
      </button>

      {/* Canvas to capture the video frame */}
      <canvas ref={canvasRef} style={{ display: 'none' }} />

      {/* Display the captured photo */}
      {capturedImage && (
        <div>
          <h3>Captured Photo:</h3>
          <img src={capturedImage} alt="Captured" style={{ maxWidth: '100%', maxHeight: '400px' }} />
        </div>
      )}
    </div>
  );
};

export default CameraCaptureWithVideo;